:root {
  --font-inter: "Inter", "Poppins", Arial, Helvetica, sans-serif;
  --spacer: 1rem;
  --limeGreen: #1db489;
  --brightRed: #dc414c;
  --facebook: #198ff5;
  --twitter: #1ca0f2;
  --instagram-start: #fdc468;
  --instagram-end: #df4996;
  --youtube: #c4032a;
  --bg: hsl(0, 0%, 100%);
  --top-bg: hsl(228, 100%, 98%);
  --bg-card: hsl(228, 50%, 96%);
  --bg-card-hover: hsl(232, 33%, 91%);
  --text-1: hsl(229, 12%, 44%);
  --text-2: hsl(230, 17%, 14%);
  --toggle-bg-start: hsl(229, 20%, 74%);
  --toggle-bg-end: hsl(229, 20%, 74%);
  --toggle: var(--bg);
}

.theme-dark {
  --toggle-bg-start: #378fe6;
  --toggle-bg-end: #3eda82;
  --bg: hsl(230, 17%, 14%);
  --toggle: var(--bg);
  --top-bg: hsl(232, 19%, 15%);
  --bg-card: hsl(229, 27%, 20%);
  --bg-card-hover: hsl(228, 26%, 27%);
  --text-1: hsl(228, 34%, 66%);
  --text-2: hsl(0, 0%, 100%);
}

/* 

#### Dark Theme

- Toggle: linear gradient hsl(210, 78%, 56%) to hsl(146, 68%, 55%)

#### Light Theme

- Toggle: hsl(230, 22%, 74%)

### Neutral

#### Dark Theme

- Very Dark Blue (BG): hsl(230, 17%, 14%)
- Very Dark Blue (Top BG Pattern): hsl(232, 19%, 15%)
- Dark Desaturated Blue (Card BG): hsl(228, 28%, 20%)
- Desaturated Blue (Text): hsl(228, 34%, 66%)
- White (Text): hsl(0, 0%, 100%)

#### Light Theme

- White (BG): hsl(0, 0%, 100%)
- Very Pale Blue (Top BG Pattern): hsl(225, 100%, 98%)
- Light Grayish Blue (Card BG): hsl(227, 47%, 96%)
- Dark Grayish Blue (Text): hsl(228, 12%, 44%)
- Very Dark Blue (Text): hsl(230, 17%, 14%) 
*/
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  background: var(--dark-bg);
  color: var(--dark-text-2);
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
  height: auto;
}

/* Content thats not visible but read by screen-readers */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
html {
  font-size: 100%;
}

body {
  font-family: var(--font-inter);
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  background-color: var(--bg);
}

h1,
h2,
h3,
h4 {
  line-height: 1.1;
}

.container {
  max-width: 70em;
  width: 90%;
  margin: 0 auto;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 1.5em 0;
  gap: 1em;
}
.header__title {
  font-weight: 700;
  border-bottom: 1px solid var(--text-1);
  padding-bottom: 1.5em;
  color: var(--text-2);
}
.header__title h1 {
  font-size: 2rem;
}
.header__title span {
  color: var(--text-1);
}
@media screen and (min-width: 40.5em) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 2em 0;
  }
  .header__title {
    border: none;
    padding-bottom: 0;
  }
}

.toggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5em;
}
.toggle span:first-child {
  color: var(--text-1);
}
.toggle span:first-child:hover {
  color: var(--text-2);
}
.toggle__switch {
  width: 50px;
  height: 25px;
  background: linear-gradient(to right, var(--toggle-bg-start), var(--toggle-bg-end));
  border-radius: 50vh;
  position: relative;
}
.toggle__switch::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  left: 2.5px;
  top: 2.25px;
  background: var(--toggle);
  border-radius: 50%;
  transition: all 0.2s ease-in-out;
}
.toggle__input:checked + .toggle__switch::after {
  left: calc(100% - 2.5px);
  transform: translateX(-100%);
}

section {
  padding: 1em 0 0;
}

.overview {
  padding-bottom: 2em;
}
.overview__title {
  margin: 1em 0;
  color: var(--text-2);
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2em;
  text-align: center;
}
@media screen and (min-width: 40.5em) {
  .cards {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}
.cards .card {
  border-radius: 4px;
  background: var(--bg-card);
  cursor: pointer;
}
.cards .card:hover {
  background: var(--bg-card-hover);
}
.cards--1 .card {
  padding: 2em 3em;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2em;
  font-size: 0.875rem;
  position: relative;
}
.cards--1 .card::after {
  content: "";
  position: absolute;
  inset: -3px 0 0;
  border-radius: 8px;
  z-index: -2;
  background-color: currentColor;
}
.cards--1 .card:nth-child(1):after {
  background: var(--facebook);
}
.cards--1 .card:nth-child(2)::after {
  background: var(--twitter);
}
.cards--1 .card:nth-child(3)::after {
  background-image: linear-gradient(to right, var(--instagram-start), var(--instagram-end));
}
.cards--1 .card:nth-child(4)::after {
  background: var(--youtube);
}
.cards--1 .card__user {
  display: flex;
  align-items: center;
  gap: 0.5em;
  color: var(--text-1);
  font-weight: 700;
}
.cards--1 .card__count {
  font-weight: 700;
  color: var(--text-2);
}
.cards--1 .card__count p {
  font-size: 3rem;
  line-height: 1.1;
}
.cards--1 .card__count span {
  color: var(--text-1);
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 4px;
}
.cards--1 .card__report {
  display: flex;
  align-items: center;
  gap: 1em;
  color: var(--limeGreen);
  font-weight: 700;
}
.cards--1 .card:nth-child(4) .card__report {
  color: var(--brightRed);
}
.cards--2 .card {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 2em;
  row-gap: 1em;
  font-size: 0.875rem;
}
.cards--2 .card > span {
  line-height: 1;
  justify-self: start;
  align-self: start;
  font-weight: 700;
  color: var(--text-1);
  text-align: left;
}
.cards--2 .card > img {
  justify-self: end;
  align-self: start;
}
.cards--2 .card p {
  justify-self: start;
  align-self: end;
  font-size: 1.75rem;
  font-weight: 700;
  line-height: 1;
  color: var(--text-2);
}
.cards--2 .card__report {
  align-self: end;
  justify-self: end;
  display: flex;
  align-items: center;
  gap: 0.5em;
  color: var(--limeGreen);
  font-weight: 700;
}
.cards--2 .card:nth-child(2) .card__report {
  color: var(--brightRed);
}
.cards--2 .card:nth-child(7) .card__report {
  color: var(--brightRed);
}
.cards--2 .card:nth-child(8) .card__report {
  color: var(--brightRed);
}